/*---------compare-popup--------*/
.popup-wrapper.compare-popup {
	display: none;
}
@media only screen and (max-width: 768px) {
	.popup-wrapper.compare-popup {
		display: block;
		width: 100%;
		top: 0;
		.inner-wrapper {
			width: 100%;
			height: 100%;
			position: relative;
			overflow-x: hidden;
			background-color: #FFFFFF;
		}
		.popup-body.slide {
			left: -100%;
			.compare-elect {
				display: none;
			}
		}
		.popup-body {
			-webkit-transition: left .3s;
			-o-transition: left .3s;
			transition: left .3s;
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
		}
		.compare-elect, .compare-info {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
		}
		.compare-elect {
			left: 0;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-orient: vertical;
			-webkit-box-direction: normal;
				-ms-flex-direction: column;
					flex-direction: column;
		}
		.compare-info {
			left: 100%;
		}
		.back-button {
			background-color: #334055;
			width: 100%;
			height: 43px;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
				-ms-flex-align: center;
					align-items: center;
			padding: 22px 5%;
			position: relative;
			-webkit-box-sizing: border-box;
					box-sizing: border-box;
			.left-icon {
				display: block;
				width: 8px;
				height: 13px;
			}
			.back-button__text {
				color: #FFFFFF;
				font: 15px/13px Montserrat Regular;
				margin-left: 15px;
			}
			.close-img {
				position: absolute;
				right: 3.5%;
				top: 50%;
				-webkit-transform: translateY(-50%);
					-ms-transform: translateY(-50%);
						transform: translateY(-50%);
				display: block;
				width: 14px;
				height: 14px;
			}
		}
		.title-panel {
			padding: 0 4%;
			-webkit-box-sizing: border-box;
					box-sizing: border-box;
			.title-text {
				margin-top: 20px;
				font: 18px/22px Montserrat Medium;
			}
			.make-choise {
				margin-top: 15px;
				font: 12px/15px Montserrat Regular;
			}
			&__bottom {
				margin-top: 30px;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-pack: justify;
					-ms-flex-pack: justify;
						justify-content: space-between;
				-webkit-box-align: center;
					-ms-flex-align: center;
						align-items: center;
			}
			.product-kind {
				font: 14px/17px Montserrat Medium;
			}
			.clean-all {
				font: 12px/15px Montserrat Regular;
			}
		}
		.second-lev .compare-products {
			height: auto;
			min-height: auto;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			padding: 0;
		}
		.compare-products {
			padding: 0 4% 0;
			position: relative;
			height: 100%;
			.list-wrapper {
				overflow: auto;
				padding-right: 12px;
				padding-top: 15px;
				box-sizing: border-box;
			}
			.compare-product-list {
				margin-bottom: 70px;
				padding-left: 6px;
			}
			.compare-product-item.disable {
				opacity: .5;
			}
			.compare-product-item {
				position: relative;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-align: center;
					-ms-flex-align: center;
						align-items: center;
				padding: 15px 30px 15px 0;
			}
		}
		.compare-item-close {
			display: block;
			width: 14px;
			height: 14px;
			position: absolute;
			right: 0;
			top: 0;
		}
		.listing-item__img-container {
			display: block;
			width: 57px;
			min-width: 57px;
			height: 57px;
			position: relative;
			margin: 0 20px;
			.item-image {
				position: absolute;
				display: block;
				top: 50%;
				left: 50%;
				-webkit-transform: translate(-50%, -50%);
					-ms-transform: translate(-50%, -50%);
						transform: translate(-50%, -50%);
				width: 100%;
			}
		}
		.listing-item__info {
			.listing-link {
				color: #000000;
				text-decoration: none;
				.info {
					font: 14px/17px Montserrat Medium;
				}
			}
		}
		.price {
			margin: 5px 0;
			.price__not-relevant {
				text-decoration: line-through;
				span {
					font: 13px/16px Montserrat Regular;
				}
				.currency {
					margin-left: 3px;
				}
			}
			.price__relevant {
				span {
					font: 16px/20px Montserrat Bold;
				}
				.currency {
					margin-left: 5px;
				}
			}
		}
		.bonus {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
				-ms-flex-align: center;
					align-items: center;
			&__icon {
				display: block;
				width: 19px;
				height: 19px;
			}
		}
		.compare-button-panel {
			width: 100%;
			height: 55px;
			border-top: 1px solid #E0E0E0;
			position: absolute;
			bottom: 0;
			left: 0;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
				-ms-flex-align: center;
					align-items: center;
			-webkit-box-pack: center;
				-ms-flex-pack: center;
					justify-content: center;
			background-color: #FFFFFF;
			.compare-button {
				display: block;
				width: 30%;
				min-width: 121px;
				padding: 10px;
				background-color: #F1583F;
				border: 0;
				border-radius: 0;
				color: #FFFFFF;
				font: 14px/17px Montserrat Semibold;
			}
			.compare-button[disabled] {
				opacity: .5;
			}
		}
		.checked-block {
			margin-right: 8px;
			margin-left: 4px;
			position: relative;
			label {
				width: 28px;
				height: 28px;
				cursor: pointer;
				position: absolute;
				top: 50%;
				left: 50%;
				-webkit-transform: translate(-50%, -50%);
					-ms-transform: translate(-50%, -50%);
						transform: translate(-50%, -50%);
				background: #FFFFFF;
				border: 1px solid #E5E5E5;
				&:after {
					content: '';
					width: 12px;
					height: 5px;
					position: absolute;
					top: 8px;
					left: 8px;
					border: 2px solid #EF4123;
					border-top: none;
					border-right: none;
					background: transparent;
					opacity: 0;
					-webkit-transform: rotate(-45deg);
						-ms-transform: rotate(-45deg);
							transform: rotate(-45deg);
				}
			}
			input[type=checkbox] {
				visibility: hidden;
				&:checked + label:after {
					opacity: 1;
				}
			}
		}
		.slide .compare-info.second-lev {
			display: block;
			height: 100%;
			.compare-information {
				.main-compare-info {
					min-height: 180px;
				}
			}
		}
		.compare-info.second-lev {
			display: none;
			.compare-products {
				&__product.first {
					border-right: 1px solid #E0E0E0;
				}
				&__product {
					-webkit-box-sizing: border-box;
							box-sizing: border-box;
					border-top: 1px solid #E0E0E0;
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-orient: vertical;
					-webkit-box-direction: normal;
						-ms-flex-direction: column;
							flex-direction: column;
					padding: 0 5%;
					width: 50%;
				}
				.listing-item__img-container {
					color: #000000;
					text-decoration: none;
					display: inline-block;
					margin: 13px auto 10px;
				}
				.item-image {
					display: block;
					height: 57px;
					width: auto;
				}
				.listing-link {
					color: #000000;
					text-decoration: none;
					display: block;
					font: 12px/15px Montserrat Medium;
				}
				.buy {
					display: block;
					border: 0;
					border-radius: 0;
					background-color: #E54415;
					color: #FFFFFF;
					font: 15px/18px Montserrat Semibold;
					padding: 10px;
					width: 80%;
					max-width: 180px;
					margin: auto auto 10px;
				}
			}
		}
		.compare-information {
			background-color: #E0E0E0;
			padding: 7px 2% 0;
		}
		.choise-panel {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			&__toggler.active {
				background-color: #FFFFFF;
				.toggler-text {
					color: #E54415;
				}
			}
			&__toggler {
				width: 50%;
				padding: 7px 0;
				.toggler-text {
					text-align: center;
				}
			}
		}
		.main-compare-info.active {
			display: block;
		}
		.main-compare-info {
			display: none;
			background-color: #FFFFFF;
			padding: 0 5%;
			overflow: auto;
			.wrapper {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
			}
			.compare-kind {
				width: 50%;
				-webkit-box-sizing: border-box;
						box-sizing: border-box;
				border-top: 1px solid #E0E0E0;
				border-bottom: 1px solid #E0E0E0;
				padding: 7px 0;
				p {
					font: 12px/15px Montserrat Regular;
					text-align: center;
				}
			}
			.compare-kind:first-child {
				border-right: 1px solid #E0E0E0;
			}
		}
		.compare-type {
			padding: 7px 0;
			position: static;
			&__text {
				font: 12px/15px Montserrat Semibold;
				text-align: center;
			}
		}
	}
}
@media only screen and (max-width: 768px) and (orientation: landscape) {
	.popup-wrapper.compare-popup {
		.popup-body.slide {
			.compare-button-panel {
				position: absolute;
			}
		}
		.compare-button-panel {
			position: fixed;
		}
		.compare-products .list-wrapper {
			height: auto;
		}
	}
}
/*---------end compare-popup--------*/